.login-container {
    position: relative;
  }
  
  .background-image {
    width: 100%;
    height: auto;
    z-index: -1;
  }
  
  .login-form {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-49%, -21.5%);
    text-align: center;
  }
  
  .transparent-placeholder {
    background-color: #ffffff;
    border: none;
    padding: 13px; /* Increase padding for both input boxes */
    margin: 19px;
    width: 230px; /* Increase width for both input boxes */
    color: #000000;
  }
  
  .transparent-placeholder::placeholder {
    color: #000000;
  }
  
  .transparent-placeholder:not(:placeholder-shown) {
    border-color: transparent;
  }
  
  .button-login {
    background-color: transparent;
    color: transparent;
    padding: 26px 160px; /* Increase padding for the button */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-top: 45px; /* Add margin-top to move the button down */
}

  
  .button-login:hover {
    background-color: transparent;
  }
  
